import { _decorator, Color, Component, Material, Node, Sprite, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('testTransition')
export class testTransition extends Component {
    @property(Material)
    mats:Material;
    @property(Sprite)
    sp:Sprite

    start() {
        this.startTransition()


    }

    startTransition(){
        let start = {num:0};
        let end = {num:255};
        const sp = this.sp;
        const color = new Color(255,255,255);
        sp.color = color;
        const mat = this.mats;
        sp.material = mat;
        tween(start).delay(0.0016).to(2.5,end, {
            onUpdate(target:any, ratio: number) {
                color.r = target.num;
                sp.color = color;
            },
        }).start();
    }

    reverse(){
        let start = {num:0};
        let end = {num:255};
        const sp = this.sp;
        const color = new Color(255,255,255);
        sp.color = color;
        const mat = this.mats;
        sp.material = mat;
        tween(start).delay(0.0016).to(2.5,end, {
            onUpdate(target:any, ratio: number) {
                color.r = target.num;
                sp.color = color;
            },
        }).start();
    }

    update(deltaTime: number) {
        
    }
}

